function AuthForm()
{
  const {user,isAuthenticated,isLoading} = useAuth0();
  let navigate = useNavigate();
  const {loginWithRedirect} = useAuth0();
  if (isAuthenticated&&!isLoading)
  {
    navigate('/dashboard');
  }
  return (
    <Auth0Provider domain="kingtechnician.us.auth0.com" clientId="xTbGmROcQrGne2nH7AAEDu1C3E8hKeBW" authorizationParams={{
      redirect_uri:window.location.origin+'/dashboard'
    }}>
    <div className = "App">
      <div className = "App-header">
        <p>Welcome!  Click the button below to get started.</p>
        <Button variant="contained" onClick ={loginWithRedirect}>Log In</Button>
      </div>
    </div>
    </Auth0Provider>
  )
}

